const dbName = 'test'
const dbVersion = 1

export default {
  // indexedDB
  indexedDB:
    window.indexedDB ||
    window.webkitindexedDB ||
    window.msIndexedDB ||
    window.mozIndexedDB,
  // name:  key: ,cursorIndex 
  store: {
    teacher: {
      name: 'teacher',
      key: 'id',
      cursorIndex: [{ name: 'teachNum', unique: false }]
    },
    student: {
      name: 'student',
      key: 'id',
      cursorIndex: [{ name: 'stuNum', unique: false }]
    }
  },
  async initDB () {
    let that = this
    let request = this.indexedDB.open(dbName, dbVersion)
    request.onerror = function () {
      console.log('')
    }

    request.onsuccess = function () {
      console.log('')
    }

    request.onupgradeneeded = function (event) {
      let db = event.target
      for (let t in that.store) {
        if (!db.objectStoreNames.contains(that.store[t].name)) {
          let objectStore = db.createObjectStore(that.store[t].name, {
            keyPath: that.store[t].key, // 
            autoIncrement: true // 
          })

          //  unique
          for (let i = 0; i < that.store[t].cursorIndex.length; i++) {
            const element = that.store[t].cursorIndex[i]
            objectStore.createIndex(element.name, element.name, {
              unique: element.unique
            })
          }
        }
      }
    }
  },
  // 
  openDB: function () {
    return new Promise((resolve, reject) => {
      let request = this.indexedDB.open(dbName, dbVersion)

      request.onerror = function (event) {
        reject(new Error('IndexedDB' + event))
      }
      request.onsuccess = function (event) {
        console.log('openDB', event.target, event.target.result)
        resolve(event.target.result)
      }
    })
  },
  // 
  deleteDB: function (table) {
    let deleteQuest = this.indexedDB.deleteDatabase(table)
    deleteQuest.onerror = function () {
      return Promise.resolve(false)
    }
    deleteQuest.onsuccess = function () {
      return Promise.resolve(true)
    }
  },
  // 
  closeDB: async function (db) {
    try {
      let d
      if (!db) {
        d = await this.openDB()
      }
      let closeQuest = d.closeDB()
      return new Promise(resolve => {
        closeQuest.onerror = function () {
          resolve(false)
        }
        closeQuest.onsuccess = function () {
          resolve(true)
        }
      })
    } catch (error) {
      return Promise.resolve(false)
    }
  },
  // add, keyerror
  add: async function (table, data) {
    try {
      let db = await this.openDB()
      let request = db
        .transaction(table, 'readwrite')
        .objectStore(table)
        .add(data)

      return new Promise((resolve, reject) => {
        request.onerror = function () {
          reject(new Error(''))
        }
        request.onsuccess = function () {
          resolve(true)
        }
      })
    } catch (error) {
      console.log(error)
      return Promise.resolve(false)
    }
  },
  // key 
  put: async function (table, data) {
    try {
      let db = await this.openDB()
      let request = db
        .transaction(table, 'readwrite')
        .objectStore(table)
        .put(data)
      return new Promise(resolve => {
        request.onsuccess = function () {
          resolve(true)
        }
        request.onerror = function () {
          resolve(false)
        }
      })
    } catch (error) {
      return Promise.resolve(false)
    }
  },
  // 
  delete: async function (table, keyValue) {
    try {
      let db = await this.openDB()
      let request = db
        .transaction(table.name, 'readwrite')
        .objectStore(table.name)
        .delete(keyValue)
      return new Promise(resolve => {
        request.onerror = function () {
          resolve(false)
        }
        request.onsuccess = function () {
          resolve(true)
        }
      })
    } catch (error) {
      return Promise.resolve(false)
    }
  },
  // 
  clear: async function (table) {
    let db = await this.openDB()
    let store = db.transaction(table.name, 'readwrite').objectStore(table.name)
    store.clear()
  },

  //     key  value keykey 
  get: async function (table, keyValue) {
    try {
      let db = await this.openDB()
      let store = db
        .transaction(table, 'readonly')
        .objectStore(table)
      let request = store.get(keyValue)

      return new Promise(resolve => {
        request.onerror = function () {
          resolve('')
        }
        request.onsuccess = function (event) {
          resolve(event.target.result)
        }
      })
    } catch (error) {
      return Promise.reject(error)
    }
  },

  //     key  value keykey 
  getCursor: async function (table, keyValue, indexCursor) {
    try {
      let db = await this.openDB()
      let store = db
        .transaction(table, 'readonly')
        .objectStore(table)

      let request = !keyValue
        ? store.openCursor()
        : indexCursor
          ? store.index(indexCursor).get(keyValue)
          : store.get(keyValue)
      let data = []
      return new Promise(resolve => {
        request.onerror = function () {
          resolve('')
        }
        request.onsuccess = function (event) {
          if (!keyValue && !indexCursor) {
            if (event.target.result) {
              data.push(event.target.result.value)
              event.target.result.continue()
            } else {
              resolve(data)
            }
          } else {
            resolve([event.target.result])
          }
        }
      })
    } catch (error) {
      return Promise.reject(error)
    }
  },
  //   , callback
  handleDataByCursor: async function (table, keyRange) {
    try {
      let kRange = keyRange || ''
      let db = await this.openDB()
      let store = db.transaction(table, 'readwrite').objectStore(table)
      let request = store.openCursor(kRange)
      return new Promise(resolve => {
        request.onerror = function () {
          resolve('')
        }
        request.onsuccess = function (event) {
          let cursor = event.target.result
          resolve(cursor)
        }
      })
    } catch (error) {
      return Promise.reject(error)
    }
  },
  // , callback
  handleDataByIndex: async function (table, keyRange, sursorIndex) {
    try {
      let kRange = keyRange || ''
      let db = await this.openDB()
      let store = db.transaction(table, 'readwrite').objectStore(table)
      let request = store.index(sursorIndex).openCursor(kRange)
      return new Promise(resolve => {
        request.onerror = function () {
          resolve('')
        }
        request.onsuccess = function (event) {
          let cursor = event.target.result
          if (cursor) {
            resolve(cursor)
          }
        }
      })
    } catch (error) {
      return Promise.reject(error)
    }
  },
  // 
  createCursorIndex: async function (table, cursorIndex, unique) {
    let db = await this.openDB()
    let store = db.transaction(table, 'readwrite').objectStore(table)
    store.createIndex(cursorIndex, cursorIndex, {
      unique: unique
    })
    return Promise.resolve()
  }
}



