// 
function checkExpress () {
  let ok = 0
  if (navigator.userAgent.indexOf('Firefox') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Opera') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Chrome') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Brave') >= 0) {
    ok = 1
  }
  console.log('express:' + ok)

  return ok
}
/**
 *   'yyyy-MM-dd hh:mm:ss'
 * */
function getFormatDateByTimestamp (timestamp, _format) {
  if (!timestamp) {
    return ''
  }
  // 000,
  if (timestamp && timestamp.toString().length === 10) {
    timestamp = parseInt(timestamp.toString() + '000')
  }

  let date = new Date(timestamp)
  let o = {
    'M+': date.getMonth() + 1, // 
    'd+': date.getDate(), // 
    'h+': date.getHours(), // 
    'm+': date.getMinutes(), // 
    's+': date.getSeconds(), // 
    'q+': Math.floor((date.getMonth() + 3) / 3), // 
    'S': date.getMilliseconds() // 
  }
  if (/(y+)/.test(_format)) {
    _format = _format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(_format)) {
      _format = _format.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return _format
}

/**
 * 
 * @returns {string}
 */
function getCurrentTime () {
  let date = new Date()
  let year = date.getFullYear()
  /* 00
  * 100   09:11:05
  */
  let month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
  let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
  let hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
  let minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
  let seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
  // 
  return year + '-' + month + '-' + day + ' ' + hours + ':' + minutes + ':' + seconds
}

/**
 *
 */
function getTimeOf (time) {
  let date = new Date(time)
  let year = date.getFullYear()
  /* 00
  * 100   09:11:05
  */
  let month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
  let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
  let hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
  let minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
  let seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
  // 
  return year + '-' + month + '-' + day + ' ' + hours + ':' + minutes + ':' + seconds
}

// 
function clearArray (array) {
  if (array.length > 0) {
    array.splice(0, array.length)
  }
}

// 
function findVideoInList (videoHash) {
  let find = false
  if (Store.state.videoList.length > 0) {
    for (let i = 0; i < Store.state.videoList.length; i++) {
      if (Store.state.videoList[i].videoHash === videoHash) {
        find = true
        return find
      }
    }
  }
  return find
}

/**
 * 
 * */
function subStringByte (str, len) {
  let regexp = /[^x00-xff]/g // 
  // 
  if (str.replace(regexp, '**').length <= len) {
    return str
  }
  // 
  let m = Math.floor(len / 2)
  for (let i = m, j = str.length; i < j; i++) {
    // 
    if (str.substring(0, i).replace(regexp, '**').length >= len) {
      return str.substring(0, i)
    }
  }
  return str
}

/**
 *  () */
function subStringByteDesc (str, len) {
  let strL = str.length
  let resl = 0
  for (let i2 = strL; i2 > 0; i2--) {
    // 127
    if (str.charCodeAt(i2) < 255) {
      resl++
    } else {
      resl += 2
    }
    // console.log(strL, i2, resl, str.substr(i2, 1))
    // 
    if (resl >= len) {
      return str.substr(i2)
    }
  }
  return str
}

// 
function getStringByteLength (str) {
  let byteLength = 0
  let unicode = 0
  let len = str.length
  if (str === '' || str == null) {
    return 0
  }

  for (let i = 0; i < len; i++) {
    unicode = str.charCodeAt(i)
    if (unicode < 255) { // 127, 255
      byteLength += 1
    } else {
      byteLength += 2
    }
  }

  // let regexp = /[^x00-xff]/g
  // byteLength = str.replace(regexp, '**').length
  return byteLength
}

