<template>
    <v-container>
        <v-layout text-xs-center wrap>
            <v-flex xs12 mb-5>
                <!--context-->
              <div style="margin-top: 9%">
                <div style="margin: 0 auto; width: 99px; height: 29px; padding-top: 9px; text-align: center; font-size: small">
                  <img alt="bliface.com" width="45px" height="45px" src="/static/image/logo-200.png">
                </div>
                <div style="margin-top: 29px">
                  <div style="margin: 0 auto;">Bliface.com</div>
                </div>
              </div>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
export default {
  name: 'externalWeb',
  data: function () {
    return {
      // 
      webUrl: 'bliface.com'
    }
  },
  mounted () {
    // 
    // 
    //   
    if (typeof this.$route.query.webUrl !== 'undefined') {
      this.webUrl = this.$route.query.webUrl
      window.parent = this
      window.location.href = this.webUrl
      console.log('key', this.webUrl)
    } else {
      // tokentoken
      this.webUrl = ''
    }
  },
  methods: {
    // 
  },
  beforeDestroy () {
    // 
  }
}
</script>

<style scoped>

</style>



